import React from 'react'
import { Epic, Tabbar, TabbarItem, View, Panel, ModalRoot, ModalPage,  Div, Checkbox, Title, FormLayout, Switch, Cell, ScreenSpinner, Button, ModalPageHeader } from '@vkontakte/vkui'
import bridge from '@vkontakte/vk-bridge'
import axios from 'axios';

// icons
import Icon28Users3Outline from '@vkontakte/icons/dist/28/users_3_outline';
import Icon28Settings from '@vkontakte/icons/dist/28/settings';
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon28Users from '@vkontakte/icons/dist/28/users';
import Icon28Send from '@vkontakte/icons/dist/28/send';


// panels
import Groups from './panels/Groups'
import Catalog from './panels/Catalog'
import SectionCatalog from './panels/SectionCatalog'
import MySubs from './panels/MySubs'
import Friends from './panels/Friends'
import Settings from './panels/Settings'
import EditSettings from './panels/EditSettings';
import ChooseCountry from './components/ChooseCountry';
import ChooseCity from './components/ChooseCity';
import SubscribeCities from './components/SubscribeCities';

class App extends React.Component {
  state = {
    activeStory: 'settings',
    activePanel: 'home',
    activeModal: null,
    popout: null,
    currentCatalog: 0,
    catalog:null,
    appId: null,

    form:null, //state для проброса значений из редактора подписки(база данных)
    obj:{
      from:null,
      to:null,
      all:false
    } , //state для проброса значений из создания и редоктирования подписки база данных

  }

  componentDidMount() {
    // Получаем данные о пользователе и каналы
    // this.setPopout(<ScreenSpinner />)
    bridge.sendPromise("VKWebAppGetUserInfo")
    .then(user => {
      this.setState({
        appId: parseInt(window.location.href.split("vk_app_id=")[1].split("&")[0], 10),
        user
      })
      axios.get("https://cors-anywhere.herokuapp.com/https://api.cheapflights.sale/api/Channels")
      .then(async data => {
        await this.sortData(data.data)
      })
    })
  }

  // Приведение каналов в адекватный вид:
  // [
  //   {
  //     catalog: "catalog name",
  //     to: [
  //       "country",
  //       {
  //         "section": "в европу",
  //         arr: [
  //           "country"
  //         ]
  //       }
  //     ]
  //   }
  // ]
  sortData = async data => {
    const catalog = []
    data.forEach(async item => {
      let index = catalog.findIndex(a => a.catalog === item.catalog)
      if(index === -1){
        catalog.push({
          catalog: item.catalog,
          to: []
        })
        index = catalog.length-1
      }
      if(item.section){
        const sectionIndex = catalog[index].to.findIndex(a => a.section === item.section)
        if(sectionIndex === -1){
          catalog[index].to.push({
            section: item.section,
            arr: [item]
          })
        }else{
          catalog[index].to[sectionIndex].arr.push(item)
        }
      }else{
        catalog[index].to.push(item)
      }
    })
    catalog.shift()
    catalog.splice(catalog.findIndex(a => a.catalog === ""),1)
    this.setState({ catalog })
    this.setPopout(null)
  }
  
  // Смена табов
  onStoryChange = (e) => {
    this.setState({ activeStory: e.currentTarget.dataset.story, activePanel: 'home' })
  }

  // Рисование попаута
  setPopout = popout => {
    this.setState({ popout })
  }

  // Открытие той или иной части света
  openCatalog = e => {
    this.setState({ 
      activePanel: 'catalog',
      currentCatalog: parseInt(e.currentTarget.id, 10),
    })
  }
  //открытие модалки
  openModal = modal => {
    this.setState({activeModal: modal});
  }

  chooseGlobal = ( data) => {
    console.log(this.state.changeDefaultName, data);
    this.setState({[this.state.changeDefaultName]: data});
  }

  // Изменение панели
  go = activePanel => {
    this.setState({ activePanel, currentSection: null })
  }

  // Открыть секцию
  chooseSection = e => {
    this.setState({ activePanel: "sectionCatalog", currentSection: parseInt(e.currentTarget.id, 10) })
  }
  
  //редактирование state(Дял формы редактирования и создания подписки на сообщества)
  onChangeGroups = (obj) => {
    console.log(obj)
   this.setState({ obj });
  }

  //редактирование state(Дял формы редокттпование)
  onChangeForm = (countrySrc,citySrc,countryDst,cityDst, request_id) => {
    this.setState({ countrySrc, citySrc, countryDst, cityDst, request_id});
  }

  choose = e => {
    this.setState({
      obj:{ ...this.state.obj,
      [e.currentTarget.name]: this.state.obj[e.currentTarget.name].map((item, i) => {
        if(i === parseInt(e.currentTarget.id, 10)){
          item.checked = e.currentTarget.checked
          return item
        }else{
          return item
        }
      })
    }
    })
    let bool = true;
    this.state.obj.from.forEach(element => {
      if(!element.checked) {
        bool = false;
      }
    });
    this.state.obj.to.forEach(element => {
      if(!element.checked) {
        bool = false;
      }
    })
    this.setState({ obj:{ ...this.state.obj,all:bool }})
  }

  chooseAll = () => {
    let bool = !this.state.obj.all;
    const { from , to} = this.state.obj;
    this.setState({
      obj: {
      ...this.state.obj,
      from: from.map((item, i) => {  
          item.checked = bool;
          return item
      }),
      to: to.map((item, i) => {  
        item.checked = bool;
        return item
    }), all:bool
  }
    })
  }

  submit = async () => {
    console.log('ok');
    const { from, to, name, groupId } = this.state.obj;
    const { catalog, currentCatalog, currentSection, user} = this.state;
    this.setPopout(<ScreenSpinner />);
    if(from.filter((item) => item.checked).length > 0 && 
      to.filter((item) => item.checked).length > 0
    ){
      let arr = []
      arr = arr.concat(from.filter((item) => item.checked)
      .map((item) => item.tag+"/"+groupId+"/"+name+"/"+catalog[currentCatalog].catalog+"/"+(currentSection ? currentSection : "Любой")))
      arr = arr.concat(to.filter((item) => item.checked)
      .map((item) => item.tag+"/"+groupId+"/"+name+"/"+catalog[currentCatalog].catalog+"/"+(currentSection ? currentSection : "Любой")))

      fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/get-tags", {
        "headers": {
          "accept": "*/*",
          "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
          "content-type": "application/json;charset=utf-8",
        },
        "body": JSON.stringify({
          user_id: String(user.id)
        }),
        "method": "POST",
      })
      .then(response => response.json())
      .then(data => {
        if(data.tags.length > 0){
          arr = arr.concat(
            data.tags.map((item) => 
              item.body+"/"+item.group_id+"/"+item.group_name+"/"+item.category+"/"+item.section
            )
          )
        }
        fetch("https://cors-anywhere.herokuapp.com/https://appvk.flights.ru/save-tags", {
          "headers": {
            "accept": "*/*",
            "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7",
            "content-type": "application/json;charset=utf-8",
          },
          "referrer": "https://appvk.flights.ru/",
          "referrerPolicy": "no-referrer-when-downgrade",
          "body": JSON.stringify({
            user_id: String(user.id),
            checkbox: arr
          }),
          "method": "POST",
        })
        .then(response => response.json())
        .then(check => {
          if(check.response === 'ok'){
            this.setState({ popout: null, activeModal: null});
          }
        })
      })
    }
  }

  render () {
    const {
      activePanel,
      popout,
      defaultCountry,
      chooseCountryText,
      changeFunction,
      changeDefaultName
    } = this.state
    const {
      from,
      to,
      all
    } = this.state.obj;
    return (
      <Epic activeStory={this.state.activeStory}  tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'groups'}
            data-story="groups"
            text="Сообщества"
          ><Icon28Users3Outline /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'settings'}
            data-story="settings"
            text="Настройки"
          ><Icon28Settings /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'subscribes'}
            data-story="subscribes"
            text="Мои подписки"
          ><Icon28HomeOutline /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'friends'}
            data-story="friends"
            text="Друзья"
          ><Icon28Users /></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'aviasales'}
            data-story="aviasales"
            text="Aviasales"
          ><Icon28Send /></TabbarItem>
        </Tabbar>
      }>
        <View id="groups" activePanel={activePanel} popout={popout} modal={
        <ModalRoot activeModal={this.state.activeModal}>
          <ModalPage id='subscribeCities' onClose={() => this.setState({ activeModal:null} )} dynamicContentHeight settlingHeight={100} header={<ModalPageHeader>Настройка</ModalPageHeader>}>
          <Div style={{ position: "relative", textAlign: "left", overflowX: "hidden", overflowY: "auto", backgroundColor:'#fff' }} className="SubscribeCities">
          <Cell indicator={<Switch checked={all} onChange={this.chooseAll}/>}>Все</Cell>
          <Title level="3" weight="bold">Показывать авиабилеты из:</Title>
          <FormLayout>
            {
              from && (
                from.map((item, index) => 
                  <Checkbox
                    key={index}
                    id={index}
                    name="from"
                    onChange={this.choose}
                    checked={item.checked}
                  >
                    {item.tag.split("#")[1].split("@")[0]}
                  </Checkbox>
                )
              )
            }
            <Title level="3" weight="bold">Показывать авиабилеты в:</Title>
            {
              to && (
                to.map((item, index) => 
                  <Checkbox
                    key={index}
                    id={index}
                    name="to"
                    onChange={this.choose}
                    checked={item.checked}
                  >
                    {item.tag.split("#")[1].split("@")[0]}
                  </Checkbox>
                )
              )
            }
            <Button mode='commerce' size='xl' onClick={this.submit}>Пременить</Button>
          </FormLayout>
        </Div>
          </ModalPage>

        
        </ModalRoot>
      }>
          <Groups
            id="home"
            state={this.state}
            setPopout={this.setPopout}
            openCatalog={this.openCatalog}
          />
          <Catalog
            id="catalog"
            state={this.state}
            setPopout={this.setPopout}
            openModal={this.openModal}
            go={this.go}
            onChangeGroups={this.onChangeGroups}
            chooseSection={this.chooseSection}
          />
          <SectionCatalog
            id="sectionCatalog"
            state={this.state}
            setPopout={this.setPopout}
            openModal={this.openModal}
            onChangeGroups={this.onChangeGroups}
            go={this.go}
          />
        </View>
        <View id="settings" activePanel={activePanel} popout={popout}>
          <Settings
            id="home"
            state={this.state}
            setPopout={this.setPopout}
            go={this.go}
            changeDefault={(chooseCountryText) => this.setState({ chooseCountryText })}
            changeDefaultText={(defaultCountry) => this.setState({ defaultCountry })}
            changeDefaultName={(changeDefaultName) => this.setState({ changeDefaultName })}
          />
          <ChooseCountry
            id="chooseCountry"
            default={defaultCountry}
            text={chooseCountryText}
            changeFunction={this.chooseGlobal}
            changeDefaultName={changeDefaultName}
            back={() => this.setState({ activePanel: 'home' })}
          />

          <ChooseCity
            id='chooseCity'
            default={defaultCountry}
            text={chooseCountryText}
            changeFunction={this.chooseGlobal}
            changeDefaultName={changeDefaultName}
            state={this.state}
            back={() => this.setState({ activePanel: 'home' })}
          />
        </View>
        <View id="subscribes" activePanel={activePanel} popout={popout}>
          <MySubs
            id="home"
            state={this.state}
            go={this.go}
            onChange={this.onChangeForm}
            changeFunction={this.chooseGlobal}
            changeDefaultName={(changeDefaultName) => this.setState({ changeDefaultName }, console.log(changeDefaultName))}
            setPopout={this.setPopout}
          />

          <EditSettings 
            id='edit-settings'
            go={this.go}
            back={() => this.go('home')}
            state={this.state}
            setPopout={this.setPopout}
            changeFunction={this.chooseGlobal}
            changeDefaultName={(changeDefaultName) => this.setState({ changeDefaultName })}
            changeDefault={(chooseCountryText) => this.setState({ chooseCountryText })}
            changeDefaultText={(defaultCountry) => this.setState({ defaultCountry })}
          />

          <ChooseCountry
            id="chooseCountry"
            default={defaultCountry}
            text={chooseCountryText}
            changeFunction={this.chooseGlobal}
            changeDefaultName={changeDefaultName}
            back={() => this.setState({ activePanel: 'edit-settings' })}
          />

          <ChooseCity
            id='chooseCity'
            default={defaultCountry}
            text={chooseCountryText}
            changeFunction={this.chooseGlobal}
            changeDefaultName={changeDefaultName}
            state={this.state}
            back={() => this.setState({ activePanel: 'edit-settings' })}
          />
        </View>
        <View id="friends" activePanel={activePanel} popout={popout}>
          <Friends
            id="home"
            state={this.state}
            setPopout={this.setPopout}
            go={this.go}
            changeFunction={this.chooseGlobal}
            changeDefaultName={(changeDefaultName) => this.setState({ changeDefaultName })}
            changeDefault={(chooseCountryText) => this.setState({ chooseCountryText })}
            changeDefaultText={(defaultCountry) => this.setState({ defaultCountry })}
          />
          <ChooseCity
            id='chooseCity'
            default={defaultCountry}
            text={chooseCountryText}
            changeFunction={this.chooseGlobal}
            changeDefaultName={changeDefaultName}
            state={this.state}
            back={() => this.setState({ activePanel: 'home' })}
          />  
        </View>
        <View id="aviasales" activePanel={activePanel} popout={popout}>
          <Panel id="home"></Panel>
        </View>
      </Epic>
    )
  }
}

export default App
